* {
  background-color: floralwhite;
}

.app {
  display: flex;
  justify-content: space-around;
  padding: 30px;


}

.game {
  width: 560px;
  height: 560px;
  display: flex;
  flex-wrap: wrap;
  border: 3px solid ;

}
.score {
  text-align: end;
  font-size: 50px;
  padding-right: 10px;
  height: 80px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(51, 51, 51) 0px 0px 0px 3px;
}


.game img {
  width: 70px;
  height: 70px;
}